"use strict";
boann.controller('brandController', ['$scope', '$http', '$window', '$state', function($scope, $http, $window, $state) {

    console.log($state.$current.url.pattern.split("/")[1] + " = case name");
    console.log($state.router.globals.$current.views.mainpage.controller + "Controller is Loaded");

    var URI     =   controler.view + "brands/index.php";
    var state   =   $state.$current.url.pattern.split("/")[1];

    switch(state){
        case "brands" :
            $scope.update = false;

            $scope.updateBrand = function(data){
                if(data){
                    var VALUES = [{data:true, data:data}];
                    $http.post(URI, VALUES, {params:{action:'updateBrand'}}).then(function(data){
                        if(data.status == 200){
                            console.log(data.data);
                            switch(data.data.data){
                                case "success" :
                                    swal("Well done!!", data.data.dataContent, "success");
                                    window.location.reload();
                                break;

                                case "error" :
                                    swal("Oeps!", data.data.dataContent, "error");
                                break;
                            }
                        }
                    });
                };
            }

            $scope.edit = function(data){
                $scope.update = true;
                $scope.form = {
                    uuid:data.uuid,
                    name:data.name,
                    brands:data.label,
                }
            }

            $scope.addBrand = function(data){
                if(data){
                    var VALUES = [{data:true, data:data}];
                    $http.post(URI, VALUES, {params:{action:'insertBrand'}}).then(function(data){
                        if(data.status == 200){
                            console.log(data.data);
                            switch(data.data.data){
                                case "success" :
                                    swal("Well done!!", data.data.dataContent, "success");
                                    window.location.reload();
                                break;

                                case "error" :
                                    swal("Oeps!", data.data.dataContent, "error");
                                break;
                            }
                        }
                    });
                };
            }

            getBrands();
        break;
    }



    function getBrands(){
        $http.get(URI, {params:{action:'getBrands'}}).then(function(data){
            if(data.status === 200){
                $scope.brands = data.data;
                console.log(data.data);
            }
        });
    }


}]);
