"use strict";
boann.controller('dealerController', ['$scope', '$http', '$window', '$state', function($scope, $http, $window, $state) {

    console.log($state.$current.url.pattern.split("/")[1] + " = case name");
    console.log($state.router.globals.$current.views.mainpage.controller + "Controller is Loaded");

    var URI     =   controler.view + "dealers/index.php";
    var state   =   $state.$current.url.pattern.split("/")[1];

    switch(state){
        case "dealers" :
            $scope.update = false;

            $scope.remove = function(data){
                if(data){
                    var VALUES = [{data:true, data:data}];
                    $http.post(URI, VALUES, {params:{action:'removeDealer'}}).then(function(data){
                        if(data.status == 200){
                            console.log(data.data);
                            switch(data.data.data){
                                case "success" :
                                    swal("Well done!!", data.data.dataContent, "success");
                                    window.location.reload();
                                break;

                                case "error" :
                                    swal("Oeps!", data.data.dataContent, "error");
                                break;
                            }
                        }
                    });
                }
            }

            $scope.updateDealer = function(data){
                if(data){
                    var VALUES = [{data:true, data:data}];
                    $http.post(URI, VALUES, {params:{action:'updateDealer'}}).then(function(data){
                        if(data.status == 200){
                            console.log(data.data);
                            switch(data.data.data){
                                case "success" :
                                    swal("Well done!!", data.data.dataContent, "success");
                                    window.location.reload();
                                break;

                                case "error" :
                                    swal("Oeps!", data.data.dataContent, "error");
                                break;
                            }
                        }
                    });
                }
            }

            $scope.edit = function(data){
                $scope.dealer = {id:data.id, name:data.name, dealer:data.label}
                $scope.update = true;
            }

            $scope.addDealer    =   function(data){
                if(data){
                    var VALUES = [{data:true, data:data}];
                    $http.post(URI, VALUES, {params:{action:'addDealer'}}).then(function(data){
                        if(data.status === 200){
                            console.log(data.data);
                            switch(data.data.data){
                                case "success" :
                                    swal("Well done!!", data.data.dataContent, "success");
                                    window.location.reload();
                                break;

                                case "error" :
                                    swal("Oeps!", data.data.dataContent, "error");
                                break;
                            }
                        }
                    });
                }else{
                    //error
                    swal("Oeps!", "Je hebt niets opgegeven!", "error");
                };
            }

            getDealers();
        break;
    }


    function getDealers(){
        $http.get(URI, {params:{action:'getDealers'}}).then(function(data){
            if(data.status === 200){
                $scope.dealers = data.data;
            }
        });
    }
}]);
